import styled from '@emotion/styled';
import {css} from '@emotion/core';

const justifyMap = {
  start: 'flex-start',
  end: 'flex-end',
  'space-between': 'space-between',
  'space-around': 'space-around',
};

const alignMap = {
  start: 'flex-start',
  end: 'flex-end',
  'space-between': 'space-between',
  'space-around': 'space-around',
  stretch: 'stretch',
};

export const box = props => css`
  display: flex;
  flex-direction: ${props.column ? 'column' : 'row'};
  justify-content: ${justifyMap[props.justify] || 'center'};
  align-items: ${alignMap[props.align] || 'center'};
  flex-wrap: ${props.wrap ? 'wrap' : ' no-wrap'};
  flex-grow: ${props.grow ? 1 : 0};
`;

const Box = styled.div`
  ${box};
`;

export const Row = styled(Box)`
  flex-direction: row;
`;

export const Column = styled(Box)`
  flex-direction: column;
`;

export default Box;
